@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400&display=swap");
@font-face {
  font-family: DubielPlain;
  src: url(/fonts/Dubiel.ttf);
}
* {
  --primary-color: black;
  --title-size: 10vw;
  --h3-font-size: clamp(0.5rem, 3vw, 1.125rem);
  --h2-font-size: clamp(1.875rem, 5vw, 5rem);
  --h6-font-size: clamp(0.375rem, 1.8vw, 0.875rem);
  --h7-font-size: clamp(0.25rem, 1vw, 0.5rem);
  --p-font-size: clamp(0.5rem, 3vw, 1.125rem);
}

html {
  background-color: black;
  color: white;
  scroll-behavior: smooth;
}

body {
  overscroll-behavior-y: none;
  /*Remove ScrollBar Entirely!*/
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  margin: 0;
  position: relative;
}

body::-webkit-scrollbar { /* WebKit for removing scrollbar*/
  display: none;
}

.loader-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#svg-loading-text {
  width: 50%;
  animation: draw-letters-fill 0.5s ease-in forwards 2s;
}

@keyframes draw-letters {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes draw-letters-fill {
  from {
    fill: transparent;
  }
  to {
    fill: white;
  }
}
header {
  padding: 1rem;
}

.title {
  margin: 0;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}

.title:hover {
  transition: all 0.5s ease-in-out;
  transform: skew(-20deg);
}

.title span {
  opacity: 0;
  transition: all 1s ease-out;
  transform: translateY(-200px);
  display: inline-block;
}

.title span.fade {
  opacity: 1;
  transform: translateY(0px);
}

h2 {
  font-family: DubielPlain;
  font-style: normal;
  font-weight: 500;
  font-size: var(--h2-font-size);
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/*
<a href="https://www.vecteezy.com/free-vector/j">J Vectors by Vecteezy</a>
*/
#logo-container {
  overflow: hidden;
}

#logo {
  height: clamp(3.5rem, 10vw, 7rem);
  transform: translateY(-250px);
  opacity: 0;
  transition: all 0.8s ease-out;
}
#logo.animated {
  transform: translate(0px);
  rotate: 0deg;
  opacity: 1;
}

a {
  text-decoration: none;
  color: white;
}

#menu-overlay {
  z-index: 5000;
  background-color: black;
  position: fixed;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-out;
}

#menu-overlay .show {
  visibility: visible;
  opacity: 1;
}

#menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1vh 2vw;
}

.menu-item {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 250;
  font-size: var(--h3-font-size);
  padding: 0.125rem 0;
  transform: translate(200px);
  transition: all 0.5s ease-out;
}
.menu-item.animated {
  transform: translate(0px);
}

.menu-item:nth-child(1) {
  transition-delay: 10ms;
}

.menu-item:nth-child(2) {
  transition-delay: 100ms;
}

.menu-item:nth-child(3) {
  transition-delay: 300ms;
}

.image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.image-container > .me {
  height: 80vh;
  filter: url(#noise);
  padding: 1vh;
  border: 0.5vh solid;
  border-color: white;
  transition: all 1s ease-out;
  transform: translateY(-70px);
  opacity: 0;
  object-fit: cover;
}
.image-container > .me.animated {
  transform: translate(0px);
  opacity: 1;
}

#about {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 250;
  font-size: var(--p-font-size);
  text-align: center;
  width: 40%;
  margin: auto;
  margin-top: 2rem;
  line-height: 1.5rem;
}

@media screen and (max-width: 992px) {
  .image-container > .me {
    width: 60vw;
    height: 60vh;
  }
  #about {
    width: 60vw;
  }
}
.scroll-tracker {
  position: fixed;
  background: linear-gradient(transparent, white, transparent);
}

.scroll-tracker.one {
  left: 0;
  width: 2px;
  height: 100px;
}

.scroll-tracker.two {
  right: 0;
  width: 2px;
  height: 100px;
} 
#mouse-trailer {
  position: fixed;
  background-color: white;
  z-index: 2000;
  height: 100px;
  width: 100px;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  border-radius: 50%;
  background: linear-gradient(to right, transparent, rgb(238, 238, 210), rgb(246, 248, 212));
  opacity: 0.4;
  filter: blur(23px);
  animation: blob-distort 20s infinite;
  pointer-events: none;
}

@keyframes blob-distort {
  0% {
    rotate: 0deg;
  }
  60% {
    scale: 1 1.1;
  }
  100% {
    rotate: 359deg;
  }
}
footer {
  font-family: "Josefin Sans", sans-serif;
}
footer h4 {
  font-weight: 250;
}

#phrases {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.quotes {
  display: flex;
  justify-content: center;
  align-items: center;
}

#phrases {
  margin-top: 8rem;
  background: white;
  height: 60vh;
  padding: 2rem;
  color: black;
}

#phrases .quotes > h3 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  font-size: 1.125rem;
}

#phrases .start-phrase, .middle-phrase, .end-phrase {
  font-family: DubielPlain;
  font-size: var(--h2-font-size);
  font-style: normal;
  font-weight: 300;
  margin: auto;
  width: 80vw;
}

h6 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  font-size: var(--h6-font-size);
  margin: 0;
}

footer {
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

footer .links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

h7 {
  font-size: var(--h7-font-size);
}